:root {
  --primaryColor: #E4C1AE;
}

body {
  margin: 0;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  height: 100%;
}

.hide {
  display: none !important;
}

.show {
  opacity: 1 !important;
}

#overlay {
  opacity: 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  transition: 0.3s;
}
#overlay #alert {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  max-width: 920px;
  min-height: 210px;
  background: #fff;
  padding-left: 6%;
}
#overlay #alert .alert-content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 24px;
  /* title */
  /* copy */
  /* button */
}
#overlay #alert .alert-content .title {
  margin: 0;
  /* T1 */
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  /* Dark #1 */
  color: #121212;
}
#overlay #alert .alert-content .copy {
  margin: 0;
  /* Body Copy */
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  /* Dark #1 */
  color: #121212;
}
#overlay #alert .alert-content .copy #counter {
  font-weight: 600;
}
#overlay #alert .alert-content .button {
  padding: 16px 40px;
  width: 128px;
  height: 48px;
  cursor: pointer;
  transition: 0.3s;
  opacity: 0;
  /* Primary #1 */
  background: var(--primaryColor);
  border-radius: 32px;
  border-style: none;
  /* Button Text */
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #FFFFFF;
  /* Inside auto layout */
  flex: none;
  order: 0;
  flex-grow: 0;
}
#overlay #alert .x-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 2.5vw;
  right: 2.5vw;
  width: 24px;
  height: 24px;
  cursor: pointer;
}
#overlay #alert .x-icon:before, #overlay #alert .x-icon:after {
  content: "";
  position: absolute;
  width: 2px;
  height: 18px;
  background: black;
  border-radius: 15px;
}
#overlay #alert .x-icon:before {
  transform: rotate(45deg);
}
#overlay #alert .x-icon:after {
  transform: rotate(-45deg);
}

